<template>
  <v-app id="inspire">
    <v-main class="blue-grey lighten-1">
      <v-container style="max-width: 450px" fill-height>
        <v-layout align-center row wrap>
          <v-flex xs12>
            <v-card>
              <div class="pa-5">
                <h1 style="text-align: center" class="mb-5">Login</h1>

                <form>
                  <v-text-field
                    label ="ID"
                    v-model="uid"
                    prepend-inner-icon="mdi-account">
                    </v-text-field>
                  <v-text-field
                    prepend-inner-icon="mdi-lock"
                    type="password"
                    label="password"
                    v-model="password"
                  >
                  </v-text-field>
                    <v-btn
                      type="submit"
                      color="blue lighten-2 text-capitalize"
                      depressed
                      large
                      block
                      dark
                      class="mb-3"
                      @click="login"
                      >
                      Sign in
                    </v-btn>
                    <v-btn
                        type="submit"
                        color="red lighten-3 text-capitalize"
                        depressed
                        large
                        block
                        dark
                        class="mb-3"
                        router-link to ="/signup"
                    >Sign up

                    </v-btn>
                </form>
                <span>
                  사용자 정보 :
                  <span>{{message}}</span>
                </span>
                <hr />
                <v-btn
                  id="getInfo"
                  @click="getInfo">
                  정보확인
                </v-btn>
                <v-btn
                  id="logout"
                  @click="logout">
                  로그아웃
                </v-btn>
                <hr/>
                <v-simple-table>
                  <tr>
                    <th>status</th>
                    <td id="status">{{status}}</td>
                  </tr>
                  <tr>
                    <th>token</th>
                    <td id="token">{{token}}</td>
                  </tr>
                  <tr>
                    <th>refresh token</th>
                    <td id="refreshtoken">{{refreshtoken}}</td>
                  </tr>
                  <tr>
                    <th>info</th>
                    <td id="info">{{info}}</td>
                  </tr>
                </v-simple-table>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>

const storage = window.sessionStorage

export default {
  data: () => {
    return{
      uid : null,
      password : null,
      message: "message test",
      status: "",
      token: "",
      refreshtoken: "",
      info: ""
    }
  },
  methods: {
    setInfo(status,token,refreshtoken,info){
      this.status = status;
      this.token = token;
      this.refreshtoken = refreshtoken;
      this.info = info;
    },
    logout(){
      storage.setItem("jwt-auth-token","");
      storage.setItem("login_user","");
      this.message = "login plz";
      this.setInfo("logout success","","");
    },
    getInfo(){
      console.log(this.uid , this.password)
      this.$http.post(
          "/user/info",{
            uid: this.uid,
            password: this.password
          },
          {
            headers:{
              "jwt-auth-token": storage.getItem("jwt-auth-token")
            }
          }
      ).then(res =>{
        this.setInfo(
            "정보 조회 성공," +
            res.headers['jwt-auth-token'],
            res.headers['jwt-auth-refresh-token'],
            JSON.stringify(res.data)
        );
      })
      .catch(e =>{
        this.setInfo("fail to info",e.response.data.msg);
      })
    },
    login(){
        storage.setItem("jwt-auth-token","");
        storage.setItem("jwt-auth-refresh-token","");
        storage.setItem("login_user","");
        this.$http.post("/auth/signin",{
          uid: this.uid,
          password: this.password
        })
      .then(res =>{
        if(res.data.status){
          this.message = res.data.data.uid + "login";
          console.dir(res.headers["jwt-auth-token"]);
          this.setInfo(
              "login complete",
              res.headers["jwt-auth-token"],
              res.headers["jwt-auth-refresh-token"],
              JSON.stringify(res.data.data)
          );
          storage.setItem("jwt-auth-token",res.headers["jwt-auth-token"]);
          storage.setItem("jwt-auth-refresh-token",res.headers["jwt-auth-refresh-token"]);
          storage.setItem("login_user",res.data.data.uid);
        }else{
          this.setInfo("","","");
          this.message = "login plz";
          alert("error! input correct id or password");
        }
      })
      .catch(e =>{
        this.setInfo("fail","",JSON.stringify(e.response || e.message));
      });

    },init(){
      if(storage.getItem("jwt-auth-token")){
        this.message = storage.getItem("login_user" + "로 로그인")
      }else{
        storage.setItem("jwt-auth-token","");
        storage.setItem("jwt-auth-refresh-token","")
      }
    },
    signup(){
      //요청
      /*
      this.$http.get("/user/signup")
          .then(res =>{
            if(res.data.status){
              console.log("data status : ",res.data.status)
            }else{
              alert("요청 실패");
            }
          })
          .catch(e =>{
            console.error("error",e);
          });

       */
    },

  },
  mounted() {
    this.init();
  }

}

</script>